  margin: 0 auto;
  padding: 10px;
  display: flex;
}

.app {
  flex-direction: column;
  width: 100%;
  min-height: 100vh;
  display: flex;
}

.main {
  flex: 1;
  width: 100%;
}

.simulador-container {
  justify-content: center;
  margin-top: 50px;
  display: flex;
}

.descripcion {
  text-indent: 2rem;
  max-width: 800px;
  line-height: 1.8;
}

.caja {
  text-align: center;
  border: 2px solid #333;
  border-radius: 8px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  max-width: 800px;
  margin: 20px auto;
  padding: 20px;
  display: flex;
}

.caja h1 {
  margin-bottom: 20px;
  font-family: sans-serif;
}

.na-logo {
  object-fit: cover;
  width: 65px;
  height: 65px;
}

.caja > div {
