  gap: 12px;
  margin-bottom: 10px;
  display: flex;
}

.navbar {
  color: #fff;
  background-color: #1a1a1a;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 1rem 2rem;
  display: flex;
}

.logo-container {
  flex-grow: 1;
  align-items: center;
  gap: 12px;
  display: flex;
}

.navbar-logo {
  object-fit: cover;
  border: 2px solid #61dafb;
  border-radius: 50%;
  width: 65px;
  height: 65px;
}

.logo-text {
  font-size: 1.5rem;
  font-weight: bold;
}

.logo-text span {
  color: #61dafb;
}

.actions {
  flex-grow: 5;
