  position: relative;
}

.menu-item:hover {
  background-color: #f3f4f6;
}

.arrow {
  float: right;
  font-size: 12px;
}

.submenu {
  background-color: #fff;
  border: 1px solid #e5e7eb;
  border-radius: 6px;
  flex-direction: column;
  gap: 6px;
  min-width: 160px;
  margin-left: 1px;
  padding: 8px;
  display: none;
  position: absolute;
  top: 0;
  left: 100%;
  box-shadow: 0 4px 10px #0000001a;
}

.has-submenu:hover .submenu {
  display: flex;
}

.github-button {
  color: #e5e7eb;
  cursor: pointer;
  background-color: #0000;
  border: none;
  border-radius: 6px;
  align-items: center;
  gap: 8px;
  padding: 6px 10px;
  font-size: 1rem;
  transition: background-color .2s, color .2s;
  display: flex;
}

.github-button:hover {
  color: #111827;
  background-color: #f3f4f6;
}

.github-button img {
  width: 25px;
  height: 25px;
}

.contenedor-Estilo {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  padding: 20px;
  display: flex;
}

.card-Estilo {
  color: #fff;
  background-color: #3498db;
  border: none;
  border-radius: 12px;
  justify-content: center;
  align-items: center;
  width: 100px;
  height: 100px;
  font-size: 28px;
  font-weight: bold;
  display: flex;
  box-shadow: 0 4px 6px #0000001a;
}

.elevada_num1 {
  background-color: green;
  transform: translateY(-20px);
  box-shadow: 0 10px 20px #0003;
}

.elevada_num2 {
  background-color: red;
  transform: translateY(-20px);
  box-shadow: 0 10px 20px #0003;
}

.carta {
  background: #add8e6;
  justify-content: center;
  align-items: center;
  width: 60px;
  height: 80px;
  margin: 5px;
  font-size: 20px;
  transition: transform .5s;
  display: inline-flex;
}

.card-Estilo {
  transition: transform .6s;
}

.elevada {
  transform: translateY(-40px);
}

.swap-left {
  transform: translate(-40px, -40px);
}

.swap-right {
  transform: translate(40px, -40px);
}

